import { Link } from "react-router-dom";
function Nav() {
  return (
    <nav className="nav-app">
      <p className="header-title">
        <a href="/">PORTFOLIO</a>
      </p>
      <ul className="header-list">
        <li>
          <a>
            <Link className="link" to="/">
              HOME
            </Link>
          </a>
        </li>
        <li>
          <a>
            <Link className="link" to="/about">
              ABOUT
            </Link>
          </a>
        </li>
        <li>
          <a>
            <Link className="link" to="/contact">
              CONTACT
            </Link>
          </a>
        </li>
      </ul>
    </nav>
  );
}

export default Nav;
